@font-face {
  font-family: 'ic-clone';
  src: url(data:font/truetype;charset=utf-8;base64,) format('truetype');
  font-weight: normal;
  font-style: normal;
}


@mixin ic-clone {
  /* stylelint-disable */
  font-family: 'ic-clone';
  /* stylelint-enable */
  &:before {
    content: '\f24d';
    font-style: normal;
  }
}

.ic-clone {
  @include ic-clone;
  display: inline-block;
}
